<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-整页加载1" class="vue-doc-element">整页加载</h2>
<p class="vue-doc-element">按钮有六种类型：主按钮、次按钮、虚线按钮、危险按钮。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-buttonsize1" class="vue-doc-element">Button Size</h2>
<p class="vue-doc-element">Button</p>

        </div>

        


        <div class="vue-doc-content">
          <p class="vue-doc-element">使用内置的 $Spin 方法可以全局加载。</p>
<p class="vue-doc-element">可以使用 Render 函数自定义显示内容</p>

        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        
          <div class="vue-doc-demo">
            <template>
    <div>
        <Button type="primary" @click="handleSpinShow">整页显示，3秒后关闭</Button>
        <Button type="primary" @click="handleSpinCustom">自定义显示内容</Button>
    </div>
</template>
          </div>
            <div class="vue-doc-code" :class="{'vue-doc-code-show':showVueDocCode}">
              <div class="vue-doc-code-copy" @click="vueDocCodeCopy">{{vueDocCodeCopyText}}</div>
              <div class="vue-doc-code-content" ref="vueDocCodeBox">
                <pre class="vue-doc-element"><code class="lang-vue"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"handleSpinShow"</span>&gt;</span>整页显示，3秒后关闭<span class="hljs-tag">&lt;/<span class="hljs-name">Button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"handleSpinCustom"</span>&gt;</span>自定义显示内容<span class="hljs-tag">&lt;/<span class="hljs-name">Button</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">methods</span>: {
            handleSpinShow () {
                <span class="hljs-keyword">this</span>.$Spin.show();
                setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
                    <span class="hljs-keyword">this</span>.$Spin.hide();
                }, <span class="hljs-number">3000</span>);
            },
            handleSpinCustom () {
                <span class="hljs-keyword">this</span>.$Spin.show({
                    <span class="hljs-attr">render</span>: <span class="hljs-function">(<span class="hljs-params">h</span>) =&gt;</span> {
                        <span class="hljs-keyword">return</span> h(<span class="hljs-string">'div'</span>, [
                            h(<span class="hljs-string">'Icon'</span>, {
                                <span class="hljs-string">'class'</span>: <span class="hljs-string">'demo-spin-icon-load'</span>,
                                <span class="hljs-attr">props</span>: {
                                    <span class="hljs-attr">type</span>: <span class="hljs-string">'load-c'</span>,
                                    <span class="hljs-attr">size</span>: <span class="hljs-number">18</span>
                                }
                            }),
                            h(<span class="hljs-string">'div'</span>, <span class="hljs-string">'Loading'</span>)
                        ])
                    }
                });
                setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
                    <span class="hljs-keyword">this</span>.$Spin.hide();
                }, <span class="hljs-number">3000</span>);
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
    <span class="hljs-selector-class">.demo-spin-icon-load</span>{
        <span class="hljs-attribute">animation</span>: ani-demo-spin <span class="hljs-number">1s</span> linear infinite;
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>

</code></pre>

              </div>
              <div class="vue-doc-code-toggle" @click="vueDocCodeToggle">
                <span class="vue-doc-code-toggle-text"></>{{showVueDocCode?"隐藏代码":"显示代码"}}</span>
                <span class="vue-doc-code-toggle-text-EN"></>{{showVueDocCode?"Hide Code":"Show Code"}}</span>
              </div>
            </div>
          


    </div>
  </template>
  <script>
    
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  export default { mixins: [mixin],
        methods: {
            handleSpinShow () {
                this.$Spin.show();
                setTimeout(() => {
                    this.$Spin.hide();
                }, 3000);
            },
            handleSpinCustom () {
                this.$Spin.show({
                    render: (h) => {
                        return h('div', [
                            h('Icon', {
                                'class': 'demo-spin-icon-load',
                                props: {
                                    type: 'load-c',
                                    size: 18
                                }
                            }),
                            h('div', 'Loading')
                        ])
                    }
                });
                setTimeout(() => {
                    this.$Spin.hide();
                }, 3000);
            }
        }
    }
</script>
  <style>
    .demo-spin-icon-load{
        animation: ani-demo-spin 1s linear infinite;
    }
</style>
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


